<template>
  <div class="scllowBox" :style="{ backgroundColor: '#F5F6FE',display: 'flex', flexDirection: 'column', paddingBottom: '10px' }">

    <div class="margin_box" :style="{  marginTop: '10px',paddingTop: '5px' }">
      <!--选择日期-->
      <change-date :style="{ backgroundColor: '#fff'}" :selectdate="selectdate" :type="type" :onchange="onchange"/>
      <!--高压低压脉搏-->
      <div :style="{ padding: '10px'}">
        <div v-for="(item,index) in bloodpressuredata" :key="index" style="padding: 5px">
          <van-divider>{{item.title}}</van-divider>
          <div style="display: flex;justify-content: space-between; padding-left: 18px; padding-right: 18px">
            <div class="div_column">
              <span class="span_data">{{item.max||'--'}}</span>
              <span>峰值</span>
            </div>
            <div class="div_column">
              <span class="span_data">{{item.min||'--'}}</span>
              <span>谷值</span>
            </div>
            <div class="div_column">
              <span class="span_data">{{item.average||'--'}}</span>
              <span>均值</span>
            </div>
          </div>
        </div>
      </div>

    </div>
    <!--本周、日、月、年数据-->
    <div class="d_h_r_item">
      <span class="span_drinkadd_title">{{ title }}</span>
      <label class="lable_item">图形分析:</label>
      <van-switch v-model="checked"  @update:model-value="onUpdateValue"  size="16px"  active-color="#15AC78" inactive-color="#dcdee0"/>
    </div>
    <!--  历史记录  -->
    <div class="margin_box scroll_box" :style="{ borderTopLeftRadius: 0, borderTopRightRadius: 0}"  >
      <div class="no_data_boold" v-show="list.length === 0">暂无数据</div>
      <HistoryRecord v-if="!checked && list.length !== 0" :list="list"  :type="type" :toYearDetial="toYearDetial"/>
      <LineECharts  :type="type" v-else-if="checked  && list.length !== 0" :list="lineList"/>
    </div>
  </div>

</template>

<script>
import moment from 'moment'
import  ChangeDate from '../../../components/ChangeDate'
import  HistoryRecord from '../../components/history-record'
import   LineECharts from '../../components/line-chart'
import { onMounted, reactive, toRefs } from "vue";
import { useRouter } from "vue-router";
import {getListByTime} from "@/service/bloodpressure";
import {formatYYMMDD} from "@/utils/util";
export default {
  name: "momtion-record",
  components: {
    ChangeDate,
    HistoryRecord,
    LineECharts
  },
  props:{
    type: {
      default:'week',
      type: String
    },
  },
  setup(props){
    const router = useRouter()

    onMounted(
        () => {
          // 赋值
          getList()
          state.title =props.type === "day"? "本日数据": props.type === "week" ? "本周数据" :  props.type === "month" ? "本月数据"  : "本年数据"
        }
    )
    // 获取数据
    const getList = async ()=>{
      const startTime =  getStartTime(props.type)
      console.log("开始时间", startTime)
      const res = await getListByTime(startTime,formatYYMMDD(state.selectdate))
      const {code,data = {}} = res
      const  obj= data
      let mAfterlist = []
        Object.keys(obj).forEach((key)=>{
          const childList = []
          const arry = obj[key]
          let heighArray = []
          let LowArray = []
          let pulseArray = []
          for (let i = 0; i < arry.length; i++) {
            heighArray.push(arry[i].highPressure)
            LowArray.push(arry[i].lowPressure)
            pulseArray.push( arry[i].heartRate)
            const itemObj = {
              yearDate:  moment(arry[i]['measureTime']).format("YYYY-MM"),
              date: props.type === "day" ? moment(arry[i]['measureTime']).format("hh:mm:ss") : moment(arry[i]['measureTime']).format("YYYY-MM-DD"),
              time: moment(arry[i]['measureTime']).format("hh:mm:ss"),
              heigh: arry[i].highPressure,
              low: arry[i].lowPressure,
              pulse:  arry[i].heartRate,
              measureTime: arry[i]['measureTime'],
            }
            childList.push(itemObj)
            state.baseInfoList.push(itemObj)
          }
          mAfterlist.push({
            low: handleData(LowArray).average,
            heigh: handleData(heighArray).average,
            pulse: handleData(pulseArray).average,
            measureTime: arry[0]['measureTime'],
            yearDate:  moment(arry[0]['measureTime']).format("YYYY-MM"),
            date: props.type === "day" ? moment(arry[0]['measureTime']).format("hh:mm:ss") : moment(arry[0]['measureTime']).format("YYYY-MM-DD"),
            child: childList
          })

        })

      if(props.type === "day"){
        state.list =  mAfterlist.length > 0 ? mAfterlist[0].child : []
      }else if(props.type !== "year"){
        state.list = mAfterlist
      }else{
        state.list = handleYearDatas(mAfterlist)
      }
      console.log("组装数据",mAfterlist, state.list)
      // 图形分析
      if(props.type === "day"){
        state.lineList = state.list
      }else{
        state.lineList = mAfterlist
      }

      // 整合高低压脉搏峰值均值。。。
      handleMaxMin()
    }

    // 开始时间
    const getStartTime = (type) => {
      let startTime = ""
      if(state.endDate === undefined){
        return  moment(state.endDate).format("YYYY-MM-DD")
      }
      let date = new Date(state.selectdate)
      switch (type){
        case 'day':
          startTime = state.selectdate
          break
        case 'week':
          startTime = date.setDate(date.getDate() - 7)
          break
        case 'month':
          startTime = date.setMonth(date.getMonth() - 1);
          break
        case 'year':
          startTime = date.setFullYear(date.getFullYear() - 1);
          break
      }
      return  moment(startTime).format("YYYY-MM-DD")
    }
    // 整合高低压脉搏峰值均值。。。
    const handleMaxMin = () => {
      const baseInfoList = props.type === "day" ?  state.list  : state.baseInfoList
      let heighArray = []
      let LowArray = []
      let pulseArray = []
      baseInfoList.map((item,index) => {
        heighArray.push(item.heigh)
        LowArray.push(item.low)
        pulseArray.push(item.pulse)
      })
      const heightObj = handleData(heighArray)
      state.bloodpressuredata[0].max = heightObj.max
      state.bloodpressuredata[0].min = heightObj.min
      state.bloodpressuredata[0].average = heightObj.average
      const lowObj = handleData(LowArray)
      state.bloodpressuredata[1].max = lowObj.max
      state.bloodpressuredata[1].min = lowObj.min
      state.bloodpressuredata[1].average = lowObj.average
      const pulseObj = handleData(pulseArray)
      state.bloodpressuredata[2].max = pulseObj.max
      state.bloodpressuredata[2].min = pulseObj.min
      state.bloodpressuredata[2].average = pulseObj.average
    }

    const handleData = (arr) => {
      if(arr.length == 0) return {
        max: 0,
        min: 0,
        average: 0
      }
      let heightMax = Math.max.apply(null,arr)
      let heightMin = Math.min.apply(null,arr);
      let heightAverage = arr.reduce((num, item, index) => {
        if(index != arr.length-1) {
          return num+item;
        } else {
          return (num+item)/arr.length;
        }
      },0);
      return {
        max: heightMax,
        min: heightMin,
        average: heightAverage.toFixed(2)
      }
    }

    // 整合年数据
    const handleYearDatas = (list) => {
      const afterData = [] // 数组
      const beforeData = list// 数组
      let tempArr = [];
      for (let i = 0; i < beforeData.length; i++) {
        if (tempArr.indexOf(beforeData[i].yearDate) === -1) {
          afterData.push({
            yearDate: beforeData[i].yearDate,
            origin: [beforeData[i]]
          });
          tempArr.push(beforeData[i].yearDate);
        } else {
          for (let j = 0; j < afterData.length; j++) {
            if (afterData[j].yearDate == beforeData[i].yearDate) {
              afterData[j].origin.push(beforeData[i]);
              break;
            }
          }
        }
      }
      console.log('年数据处理------', afterData)
      return afterData
    }

    /**
     * 跳入年详情月列表界面
     */
    const toYearDetial = (item) => {
      const json =  JSON.stringify(item.origin)
      console.log("json",json)
      router.push({ path:'/year-blood-List', query: { listJson: json, date: item.yearDate}})
    }

    // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
    const state = reactive({
      endDate: undefined,
      lineList:[],
      todo: '',
      sumCount: 1200,
      selectdate: new Date(),
      list: [],
      baseInfoList:[], // 将每日数据拆开成每次测量集合
      title: '本周数据',
      checked: false, // 是否开启图形分析
      bloodpressuredata: [{
        title:'高压',
        max:0,
        min:0,
        average:0
      },
        {title:'低压',
          max:0,
          min:0,
          average:0},
        {title:'脉搏',
          max:0,
          min:0,
          average:0}],
    })


    // 图形分析开关
    const onUpdateValue = (newValue) => {
        state.checked = newValue;
    };

    const onchange=(data) =>{
      state.selectdate = data
      state.endDate = data
      getList()
    }
    return {
      ...toRefs(state),
      onchange,
      onUpdateValue,
      toYearDetial
    }
  }
}
</script>

